import React, { useState } from 'react'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'

export const SignUp = () => {
  const [formData, setFormData] = useState({})

  const navigate = useNavigate()

  const handleInfo = (e) =>{
    setFormData({ ...formData, [e.target.name]:e.target.value })
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    axios.post('http://localhost:4000/api/user', formData)
    .then(res => {
      console.log(res.data)
      alert("Sign up Success")
      navigate('/home')
    })
    .catch(err => console.log(err.message))
  }

  return (
    <div className="container row mt-4">
       
       <form className='col-md-6 m-auto' onSubmit={handleSubmit}>
       <h1>Sign Up</h1>
  <div className="form-group mt-2">
    <label htmlFor="full-name">Full Name</label>
    <input onChange={handleInfo} name='fullName' type="text" className="form-control" id="full-name" placeholder="Enter Your Full Name"/>
  </div>
  <div className="form-group mt-2">
    <label htmlFor="email">Email</label>
    <input onChange={handleInfo} name='email' type="email" className="form-control" id="email" placeholder="Enter A Valid Email"/>
  </div>
  <div className="form-group mt-2">
    <label htmlFor="password">Password</label>
    <input onChange={handleInfo} name='password' type="text" className="form-control" id="password" placeholder="Enter A Strong Password"/>
  </div>
  <div className="form-group mt-2">
    <label htmlFor="mobile">Mobile</label>
    <input onChange={handleInfo} name='mobile' type="Number" className="form-control" id="mobile" placeholder="Enter Your Mobile Number"/>
  </div>
  <div className="form-group mt-2">
    <label htmlFor="address">Address</label>
    <input onChange={handleInfo} name='address' type="text" className="form-control" id="address" placeholder="Enter Your Address"/>
  </div>
  <button type="submit" className="btn btn-primary mt-2">Submit</button>
</form>

    </div>
  )
}
